body,
h1,
h2,
h3,
ul,
p {
  margin: 0;
}

.page {
  font-family: 'Inter', sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #292929;
  
  scroll-behavior: smooth;

 &__body {
  margin: 0;
  
  &--with-menu {
    overflow: hidden;
  }
 } 

 &__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  transform: translateX(-100%);
  transition: transform 0.3s;

  &:target {
    transform: translateX(0);
  }
 }

 &__section {
  margin-bottom: 120px;

 }

 &__section--title {
  margin: 0 0 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  text-align: center;
  letter-spacing: -1.5px;

  color: #292929;

  @include onDesktop {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 0 0 64px;
  }
 }
}