@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top-actions {
  --icon-width: 18px;

  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  justify-content: space-between;
  align-content: center;
  gap: 30px;
  align-items: center;

  @include onDesktop {
    --icon-width: 24px;
    gap: 40px;
  }
} 
